import React, {Component} from 'react';
import {Container, Tab, Row, Col, Nav, Button} from 'react-bootstrap'
import '../App.css';
import Footer from '../Components/AppFooter';
import img from '../images/aditional.png';

export default class AppHomework extends Component {
  render(){
    return (
      <>
      <Container>
        <p style={{height: '70px' }}>
        </p>
        <Tab.Container id="ledt-tabs-example" defaultActiveKey="1">
          <Row>
            <Col sm={3}>
              <Nav variant="pills" className="flex-column mt-3">
                <Nav.Item>
                  <Nav.Link eventKey="1">HTML+CSS3</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="2">BOOTSTRAP</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="3">JAVASCRIPT</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="4">PROJECT</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                  <Nav.Link eventKey="5">ADITIONAL</Nav.Link>
                </Nav.Item>
              </Nav>
            </Col>
            <Col sm={9}>
              <Tab.Content className="mt-3">
                <Tab.Pane eventKey="1">
                    <div>
                      <h1>Задание по верстке блоками</h1>
                        <h3 class="App-homework">Цель:</h3>
                        <h5>
                          Создать страницу блога, используя техники CSS3.
                          У нас уже есть HTML файл, нужно с помощью  CSS его красиво оформить.
                        </h5>
                        <h3 class="App-homework">Выполнение:</h3>
                        <p class="App-homework" >
                          Первым делом скачиваем необходимый архив с заданием и материалами для него.
                        </p>
                        <p>
                          <Button variant="outline-primary" href="https://edummf.bsu.by/pluginfile.php/9747/mod_assign/introattachment/0/%D0%B2%D0%B5%D1%80%D1%81%D1%82%D0%BA%D0%B0%20css%20float.rar?forcedownload=1">
                            АРХИВ
                          </Button>
                        </p>
                        <p class="App-homework">
                          В нашем архиве есть 2 папки и 4 файла.
                          Из них нам понадобятся:
                            <p class="App-homework-list">1. Lecture3 шаблон с текстом.html</p>
                            <p class="">2. simple.css</p>
                            <p class="">3. Lecture3 instruction.htm</p>
                        </p>
                        <p class="App-homework">Основа - это наш HTML. Его изменять не нужно, только использовать
                        его классы в simple.css.
                        Открываем Lecture3 instruction.htm, там инстуркция и в конце пример окончательного блога.
                        Приступаем к выполнению этих рекомендаций и инструкций.
                        </p>

                    </div>
                </Tab.Pane>
              </Tab.Content>
              <Tab.Content>
                <Tab.Pane eventKey="2">
                  <div>
                    <h1>Задание по Bootstrap</h1>
                      <h3 class="App-homework">Цель:</h3>
                      <h5>
                        Cоздать landing страницу с выпадающим меню при наведении, а не при щелчке,
                        как по умолчанию делает bootstrap. Для этого, надо добавить в несколько
                        пунктов меню второй уровень и задать им нужные классы:
                        <i> dropdown, dropdown-menu,  dropdown-item.</i>
                      </h5>
                      <h3 class="App-homework">Выполнение:</h3>
                      <p>На сайте вы найдете пошаговую инструкцию для создания простой
                      landing страницы.
                      </p>
                      <p>
                        <Button variant="outline-primary" target="_blank" href="https://webdesign.tutsplus.com/tutorials/how-to-build-a-landing-page-with-bootstrap-4--cms-32519">
                          САЙТ
                        </Button>
                      </p>
                      <p>
                        В этой статье о том как сделать выпадающее меню.
                      </p>
                      <p>
                        <Button variant="outline-primary" target="_blank" href="https://webdesign.tutsplus.com/tutorials/how-to-make-the-bootstrap-navbar-dropdown-work-on-hover--cms-33840">
                          СТАТЬЯ
                        </Button>
                      </p>
                  </div>
                </Tab.Pane>
              </Tab.Content>
              <Tab.Content>
                <Tab.Pane eventKey="3">
                <div>
                  <h1>Задание по JavaScript</h1>
                    <h3 class="App-homework">Цель:</h3>
                    <h5>
                      Cоздать один html файл с внедренными стилями и JS скриптами.
                    </h5>
                    <h6>1</h6>
                    <p>
                    Добавить на вашу HTML страницу средствами JS таблицу в которой разместить
                    данные, полученные <a  target="_blank" href="https://json-schema-faker.js.org/">отсюда</a>.
                    В вашем сайте (в файле js) создастся массив объектов. Вывести полученный массив
                    в html-таблицу.
                    Запрещается использовать методы типа <i>document.write(...)</i>.
                    Я рассчитываю на то, что вы будете строить DOM с помощью методов
                    <i>document.createElement, node.append</i> и т.д.
                    (<a target="_blank"  href="https://learn.javascript.ru/modifying-document">читайте</a>)
                    </p>
                    <p>
                    Важно! подписи столбцов должны совпадать с именами полей в массиве.
                    Надо их прочитать, например,
                    из первого элемента и потом, соответственно, генерировать столбцы в
                     таблице и обращаться к соответствующим именам при расчётах и при выполнении 5-го шага.
                    </p>
                    <h6>2</h6>
                    <p>
                    С помощью CSS выделить в таблице четные строки одним фоновым цветом,
                     нечетные другим (псевдокласс nth-child). При наведении на строку
                     выделять ее новым цветом (псевдокласс hover:) Выделение
                     столбца - сложнее. Обычно используют псевдоэлемент
                     :after (ищите реализации в сети).
                    </p>
                    <h6>3</h6>
                    <p>
                      Не обращаясь к исходному массиву, а работая с DOM,
                      посчитать "кол-во" по всем товарам и вывести его под таблицей.
                    </p>
                    <h6>4</h6>
                    <p>
                      Под таблицей надо динамически обновлять какое-нибудь
                      аггрегированное значение (напр. сумма, среднее и т.п. по какому-то столбцу).
                    </p>
                    <h6>5</h6>
                    <p>
                      Под таблицей разместить форму добавления объекта
                      (названия полей нельзя просто захардкодить. Их нужно брать
                      из переменных, созданных в первом шаге). По нажатию на кнопку
                      "Добавить" должен добавиться объект в массив и строку в
                      таблицу. ВАЖНО: массив и таблица должны быть синхронизированы и
                      значения из пункта 3 должны автоматически пересчитываться.
                      Далее: в каждой строке таблицы добавить колонку для действий.
                      Задание попроще: сделать много кнопок для удаления каждой строки,
                      посложнее: кнопки удаления и редактирования. ВАЖНО:
                      опять-таки, массив и таблица после данных действий должны
                      быть синхронизированы. Если кто будет делать редактирование, то
                      надо использовать ту же форму, что и для создания объектов.
                    </p>
                </div>
                </Tab.Pane>
              </Tab.Content>
              <Tab.Content className="mt-3">
                <Tab.Pane eventKey="4">
                <div>
                  <h1>Проект</h1>
                    <h3 class="App-homework">Цель:</h3>
                    <h5>
                      Создать свой сайт на любую тематику с требованиями
                      данными ниже.
                    </h5>
                    <h6>1</h6>
                    <p>
                      На вашем сайте должен быть адаптивный дизайн стартовой страницы
                      (продемонстрировать знание CSS).
                    </p>
                    <h6>2</h6>
                    <p>
                      На вашем сайте должна быть хоть одна страница с кодом JS
                      (Ajax отправление информации на сервер и приём данных с сервера).
                    </p>
                </div>
                </Tab.Pane>
              </Tab.Content>
              <Tab.Content className="mt-3">
                <Tab.Pane eventKey="5">
                <div>
                  <h1>Дополнительное задание</h1>
                    <h3 class="App-homework">Цель:</h3>
                    <h5>
                      С помощью инструкций сверстать страницу четырьмя способами.
                    </h5>
                    <p>
                      <Button variant="outline-primary" target="_blank" href="https://habr.com/ru/post/202408/">
                        DIV
                      </Button>
                    </p>
                    <p>
                      <Button variant="outline-primary" target="_blank" href="https://habr.com/ru/post/211032/">
                        BOOTSTRAP
                      </Button>
                    </p>
                    <p>
                      <Button variant="outline-primary" target="_blank" href="https://habr.com/ru/post/228523/">
                        WORDPRESS
                      </Button>
                    </p>
                    <p>
                      <Button variant="outline-primary" target="_blank" href="https://habr.com/ru/post/256635/">
                        JOOMLA
                      </Button>
                    </p>
                    <h6>
                      Bнешний вид страницы HTML должен быть таким.
                    </h6>
                    <img src={img} alt="img" class="App-image-additional"/>
                    <h6 class="App-homework-h6">
                      Учтите при работе следующий комментарий
                    </h6>
                    <p>
                      Такой код как в этих статьях совершенно спокойно подходит
                      для написания страниц, которые не нужно будет затем часто
                      менять, но не для проектов, которые постоянно развиваются.
                      Использование стилей для id элементов не есть хорошо. Необходимо
                       принять, что стили для id — это плохо.
                      При последующей поддер жке такого сайта Вам придётся пойти
                      в inline стили элементов.
                      Для стилей лучше использовать class.
                    </p>
                    <p>
                      Главная задача хорошо сверстанной страницы — возможность перемещения
                      блоков в любое место. Простой пример — у вас есть блок новости.
                      Теперь в блок новости нужно вставить не только сами новости, но и
                      скажем, блок погоды. В данном случае, если у вас используется
                      каскадная верстка (.class div div и иже с ними), то Вам придется
                      переписывать верстку для погоды и новостей. В большом
                      проекте — это большое время и затраты. (сменили в одном месте —
                      вполне может упасть в другом. Опять придется «выплывать» за
                      счет inline стилей. (либо !important) Проект станет менее понятным.
                      При верстке проектов лучше подходить с точки зрения какой-либо из
                      методологий верстки — (Для российского сегмента интернета
                      популярной методологией является <a target="_blank" href="https://habr.com/ru/company/yandex/blog/438598/">БЭМ</a>
                      , разработанный в недрах
                      Яндекса)
                    </p>
                </div>
                </Tab.Pane>
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
        <Footer/>
      </Container>
      </>
    );
  }
}
