import React, {Component} from 'react';
import '../App.css';
import Footer from '../Components/AppFooter';


export default class About extends Component {
  render(){
    return (
      <div class="App-about">
        <h1>О САЙТЕ</h1>
        <p class="App-about-p">
          Сайт был создан Чикалинским Никитой как финальный
          проект по веб программированию и представляет
          собой информацию о домашних заданиях для
          студентов БГУ, механико-математического факультета,
          1 курса 2 семестра. Сайт содержит
          задания и ссылки на дополнительные
          источники для изучения.
        </p>
        <Footer/>
      </div>
    );
  }
}
