import React, {Component} from 'react';
import email from '../images/email.png';
import phone from '../images/phone.png';
import location from '../images/location.png';
import {Container, Row, Col, Form, Button} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

export default class Contact extends Component {

  render(){
    return (
  <div class="App-contacts">
    <Container fluid="md">
        <Row className="justify-content-md-center">
          <Col xs="3" ><img height="40" src={email}/></Col>
          <Col style={{height: '50px' }}>
            <a
              className="App-link"
              target="_blank"
              href="https://mmf.bsu.by/ru/cathedras/web-technologies-and-computer-modeling/employees-wtcm/barvenov-sergej-aleksandrovich/"
             >Bars@bsu.by</a>
          </Col>
        </Row>
        <Row></Row>
        <Row className="justify-content-md-center">
          <Col xs="3"><img height="40" src={phone}/></Col>
          <Col style={{height: '50px' }}><a href="tel:+375292095361">(8 017) 209-53-61</a></Col>
        </Row>
        <Row className="justify-content-md-center">
          <Col xs="3"><img height="40" src={location}/></Col>
          <Col style={{height: '50px' }}>пр. Независимости 4, к. 423</Col>
        </Row>
        <h2 class="App-email-h2">Оставить отзыв</h2>
      <div style={{width: '50%'}}>

        <Form onSubmit={this.handleChange}>
          <Form.Group controlId="formBasicEmail">

            <Form.Label for="email">Email</Form.Label>
            <Form.Control
              type="email"
              name="email"
              onChange={this.handleChange}
              placeholder="Enter Email"/>
          </Form.Group>

          <Form.Group controlId="formBasicPassword">
            <Form.Label>Text</Form.Label>
            <Form.Control
              as="textarea"
              name="message "
              onChange={this.handleChange}
              rows="3"/>
          </Form.Group>
          <Button variant="outline-primary">SEND</Button>
        </Form>
      </div>
    </Container>
  </div>
    );
  }
}
